.listItemComponent {
    background-color: white;
    padding: 10px 5px;
    border-bottom: 1px solid #c4c4c4;
    flex: 1 1;
    min-width: 100;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
  
  }
  
  .label {
    display: flex;
    align-items: center;
  }
  
  .label > svg {
    width: 24px;
    min-width: 24px;
    max-width: 24px;
    height: 24px;
    min-height: 24px;
    max-height: 24px;
    fill: #083749;
  }
  
  .name {
    font-weight: bold;
    font-size: 17px;
    line-height: 27px;
    color: #083749;
    margin-left: 4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: 'Assistant', sans-serif;
  }
  
  .description {
    margin-left: 2px;
    margin-top: 4px;
    color: #959595;
    font-size: 12px;
    line-height: 17px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
   
.complexItemContent {
    color: white;
    flex: 1 1;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }
  
  .backgroundLeft {
    align-items: flex-end;
  }
  
  .content {
    display: flex;
    flex-flow: column;
    align-items: center;
    font-weight: 300;
    font-size: 14px;
    width: 64px;
    max-width: 64px;
    text-align: center;
    padding: 4px;
  }
  
  .content > svg {
    fill: white;
    width: 32px;
    height: 32px;
    margin-top: -4px;
  }
  


