}

.swipeLeft-divs, .swipeRight-divs {
    width: 100vw;
    font-weight: bold;
    /* 
    line-height:
    usually used for each line hight when have lots of text in an element
    BUT
    when have single row it can be used for BOTH
    1. centerized the text VERTICALY. 
    2. define the element height.

    (This only works for one line of text !!!).
     ↓ */
    line-height: 8vh;
}

.swipeLeft-divs {
    /*from right2left*/
    background: green;
    text-align: right;
}

.swipeRight-divs {
    /*from left2right*/
    background: red;
    text-align: left;
}

